<template>
<div class="my_order_box">
    <div v-wechat-title="$route.meta.title"></div>
    <div class="order_wrap_title">
        <ul>
            <li v-for="(item,index) in typeList">
                <p style="width: 33%;margin: 0 auto;" :class="{order_title_on:index==current}" @click="selectStyle(item,index)">{{ item }}</p>
            </li>
        </ul>
    </div>
    <ul class="order_wrap" v-if="showType === 1">
        <!--  style="color:#333333;"  -->
        <li  style="height:6.12rem;" v-for="(item,index) in orderList" :code="item.or_id">
            <div class="order_up">
                <p v-if="item.or_status === 0" style="color:#333333;"></p>
                <p v-else-if="item.or_status === 2"></p>
                <p v-else style="color:#333333;"></p>
                <img  @click="cancalAdv(item.or_id,1)" src="./img/delete.png" alt="">
            </div>
            <div class="order_center">
                <div class="order_title">
                    <span>:</span>
                    <span>{{ item.or_number }}</span>
                </div>
                <div class="order_title">
                    <span>:</span>
                    <span>{{ item.adv_title }}</span>
                </div>
                <div class="order_title">
                    <span>:</span>
                    <span>{{ item.or_total_money}}.00</span>
                </div>
                <div class="order_title">
                    <span>:</span>
                    <span>{{ item.month}}</span>
                </div>
            </div>
            <div class="order_down">
                <p class="order_check_detail" @click="advDetail(item.or_status,item.or_id,0)"></p>
            </div>
        </li>
        <!--  v-for="(item,index) in orderList1"  ="showType === 2"-->
    </ul>

    <ul class="order_wrap" v-else>
         <li style="height: 4.2rem;" v-for="(item,index) in orderList1" :code="item.o_id">
            <div class="order_up">
                <p v-if="item.o_status === 3"></p>
                <p v-else></p>
                <img  @click="cancalAdv(item.o_id,2)" src="./img/delete.png" alt="">
            </div>
            <div class="order_center" style="height:48%;">
                <div class="order_title">
                    <span>:</span>
                    <span>{{ item.o_number }}</span>
                </div>
                <div class="order_title">
                    <span>:</span>
                    <span>{{ item.h5_src_title }}</span>
                </div>

            </div>
            <div class="order_down">
                <p  @click="advDetail(item.o_status,item.o_id,1)" class="order_check_detail"></p>
            </div>
        </li>
    </ul>

    <!--  -->
        <div class="alert_collect_wrap" v-show="show_cancel">
            <div class="alert_box">
                <img src="./img/cancel.png" alt="">
                <p></p>
                <div class="alert_bottom">
                    <p @click="sureCancel()" class="sureCancel"></p>
                    <p @click="cancelCancel()" class="cancelCancel"></p>
                </div>
            </div>
        </div>
</div>
</template>

<script>
import { MessageBox, Indicator } from 'mint-ui'
export default {
  components: {
    MessageBox,
    Indicator
  },
  data () {
    return {
      show_cancel: false,
      current: 0,
      typeList: ['', ''],
      orderList: [], // 
      showType: 1, // 
      orderList1: [],
      cancelIdZ: -1, // id 
      cancelIdS: -1, // 
      cancelType: -1 //  
    }
  },
  methods: {
    advDetail: function (status, id, type) {
      this.$router.push({
        path: '/waitPay',
        query: {
          status: status,
          id: id,
          type: type
        }
      })
    },
    advList: function (typevalue) {
      this.$axios.get('/api/wxpub/orders_controller/orderIndex')
        .then((res) => {
        //   console.log(res.data.data)
          Indicator.close()
          //   if(res.data.data.length === 0){
          //       console.log('')
          //   }else {
          //       this.orderList = res.data.data
          //   }
          let obj = [
            [
              {
                'or_id': 34,
                'or_number': '152490726317937',
                'adv_title': '',
                'or_total_money': 30,
                'or_status': 2,
                'adv_type': 1,
                'month': 1
              },
              {
                'or_id': 68,
                'or_number': '152698891761742',
                'adv_title': '',
                'or_total_money': 744,
                'or_status': 0,
                'adv_type': 1,
                'month': 6
              }
            ],
            [
              {
                'h5_src_title': '',
                'o_number': '152186214750676',
                'o_status': 3,
                'o_id': 8,
                'adv_type': 2
              },
              {
                'h5_src_title': '2',
                'o_number': '152186214750676',
                'o_status': 3,
                'o_id': 8,
                'adv_type': 2
              }
            ]
          ]
          if (typevalue === 1) {
            // 
            this.orderList = obj[0]
          } else {
            // 
            this.orderList1 = obj[1]
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    cancalAdv: function (orderId, type) {
      this.show_cancel = true
      if (type === 1) {
        this.cancelIdZ = orderId
        this.cancelType = 1
      } else {
        this.cancelIdS = orderId
        this.cancelType = 2
      }
    },
    cancelCancel: function () { //  
      this.show_cancel = false
    },
    sureCancel: function () { // 
      this.show_cancel = false
      this.dianji = false
      if (this.cancelType === 1) { // 
        this.orderList.splice(this.orderList.findIndex(item => item.or_id === this.cancelIdZ), 1)
      } else {
        this.orderList1.splice(this.orderList1.findIndex(item => item.o_id === this.cancelIdS), 1)
      }
      this.show_cancel = false
    },
    selectStyle (item, index) {
      this.current = index
      Indicator.open()
      if (index === 0) {
        this.showType = 1
        this.advList(1)
      } else {
        this.showType = 2
        this.advList(2)
      }
    }
  },
  beforeCreate: function () {

  },
  created: function () {
    // console.log('----------------------------------')
    Indicator.open()
    this.advList(this.showType) //    1   2
  },
  beforeMount: function () {
    // console.log(this.$route.query.type)
  },
  mounted: function () {
    // this.timeDown()
    // console.log(this.$route.query.type)/
  },
  deactivated () {
    Indicator.close()
  }
}
</script>

<style lang="css" scoped>
@import './css/my_order.css';
.my_order_box{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #F3F6F5;
}
/*  */
.alert_collect_wrap {
    width: 100%;
    height: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: rgba(0,0,0,.3);
}
.alert_box {
    width: 60%;
    height: 4.5rem;
    background-color: #fff;
    border-radius: 0.3rem;
    margin-left: 20%;
    margin-top: 57%;
    text-align: center;
}
.alert_box img {
    width: 3rem;
    height: 2rem;
    margin-top: 0.6rem;
}
.alert_box p {
    font-size: 0.4rem;
    color: #666666;
}
.alert_bottom {
    width: 100%;
    height: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    margin-top: 0.2rem;
}
.alert_bottom p:nth-of-type(1) {
    background-color: #fff;
    border: 0.02rem solid #3e87f3;
    color: #3e87f3;
}
.alert_bottom p {
    width: 2rem;
    height: 0.7rem;
    background-color: #3e87f3;
    border-radius: 0.5rem;
    line-height: 0.7rem;
    color: #fff;
    font-size: 0.35rem;
}
.order_wrap_title{
    width: 100%;
    height: 1.13rem;
    line-height: 1.13rem;
    text-align: center;
    background-color: #fff;
}
.order_wrap_title li{
    width: 49%;
    height: 100%;
    float: left;
    font-size: 0.4rem;
    color: #666666;
}
.order_wrap li:first-child{
    height:4.2rem;
}
.order_title_on{
    color: #5286EC;
    border-bottom: 0.08rem solid #5286EC;
}
.order_title_on ul li p{
    display: block;
    width: 33%;
    margin: 0 auto;
}
</style>



