import Vue from 'vue'
import Router from 'vue-router'
import Phone from '@/components/phone/phone' // 
import ActDetail from '@/components/act_detail/act_detail' // 
import CheckMore from '@/components/check_more/check_more' // 
import CreatePhoneadv from '@/components/create_phone_adv/create_phone_adv' // 
import LandingPage from '@/components/landing_page/landing_page' // 
import MakeAdv from '@/components/make_adv/make_adv' // 
import MyActivity from '@/components/my_activity/my_activity' // 
import MyAdv from '@/components/my_adv/my_adv' // 
import PayType from '@/components/pay_fs/pay_fs' // 
import PayDetail from '@/components/pay_type/pay_detail' // 
import RedPackets from '@/components/red_packets/red_packets' // 
import ReleaseActivity from '@/components/release_activity/release_activity' // 
import NumDeatil from '@/components/num_detail/num_detail' //     
import TabOne from '@/components/num_detail/tab_one' // 
import TabTwo from '@/components/num_detail/tab_two' // 
import TabThree from '@/components/num_detail/tab_three' // 
import TabFour from '@/components/num_detail/tab_four' // 
import PlayTime from '@/components/play_time/play_time' // 
import SiterPrivilege from '@/components/siter_privilege/siter_ privilege' // 
import MyCustomer from '@/components/siter_privilege/my_customer' // 
import ZhanDetail from '@/components/zhan_detail/zhan_detail' // 
import SiteDetails from '@/components/site_details/site_details' //    
import SiteMsg from '@/components/site_details/site_msg' //  
import MyOrder from '@/components/my_order/my_order' // 
import WaitPay from '@/components/wait_pay/wait_pay' //   
import ConsumptionDetails from '@/components/consumption_details/consumption_details' //      
import MouthBill from '@/components/month_bill/month_bill' // 
import MyWallet from '@/components/my_wallet/my_wallet' // 
import MyParse from '@/components/my_purse/my_purse' // 
import Tixian from '@/components/my_purse/withdrawal' // 
import QuestionResult from '@/components/question_result/question_result' // 
import QuestionHistory from '@/components/question_result/question_history' // 
import JoinApply from '@/components/join_apply/join_apply' // 
import ReadJoin from '@/components/join_apply/read_join' // 
import UserCenter from '@/components/user_center/user_center' // 
import DoorOrder from '@/components/door_order/door_order' // 
import SiteMap from '@/components/zhan_detail/siteMap' //  

Vue.use(Router)
Vue.use(require('vue-wechat-title'))

export default new Router({
  routes: [
    { // 
      path: '/',
      // name: 'userCenter',
      component: UserCenter,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/phone',
      name: 'Phone',
      component: Phone,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/actDetail',
      name: 'ActDetail',
      component: ActDetail,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/checkMore',
      name: 'checkMore',
      component: CheckMore,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/createPhoneadv',
      name: 'createPhoneadv',
      component: CreatePhoneadv,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/landingPage',
      name: 'landingPage',
      component: LandingPage,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/makeAdv',
      name: 'makeAdv',
      component: MakeAdv,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/myActivity',
      name: 'myActivity',
      component: MyActivity,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/myAdv',
      name: 'myAdv',
      component: MyAdv,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/payType',
      name: 'payType',
      component: PayType,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/payDetail',
      name: 'payDetail',
      component: PayDetail,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/red',
      name: 'red',
      component: RedPackets,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/releaseActivity',
      name: 'releaseActivity',
      component: ReleaseActivity,
      meta: {
        title: ''
      }
    },
    { //    
      path: '/numDetail',
      name: 'numDetail',
      meta: {
        title: ''
      },
      component: NumDeatil,
      children: [
        {
          path: '/numDetail/',
          name: 'tab',
          component: TabOne
        },
        {
          path: '/numDetail/tab1',
          name: 'tab1',
          component: TabOne
        },
        {
          path: '/numDetail/tab2',
          name: 'tab2',
          component: TabTwo
        },
        {
          path: '/numDetail/tab3',
          name: 'tab3',
          component: TabThree
        },
        {
          path: '/numDetail/tab4',
          name: 'tab4',
          component: TabFour
        }
      ]
    },
    { // 
      path: '/playTime',
      name: 'playTime',
      component: PlayTime,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/zhanDetail',
      name: 'zhanDetail',
      component: ZhanDetail,
      meta: {
        title: ''
      }
    },
    { //  
      path: '/siteDetails',
      naem: 'siteDetails',
      component: SiteDetails,
      meta: {
        title: ''
      }
    },
    { //  
      path: '/siteMsg',
      name: 'siteMsg',
      component: SiteMsg,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/siterPrivilege',
      name: 'siterPrivilege',
      component: SiterPrivilege,
      meta: {
        title: ''
      }
    },
    {
      path: '/myCustomer',
      name: 'myCustomer',
      component: MyCustomer,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/myOrder',
      name: 'myOrder',
      component: MyOrder,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/waitPay',
      name: 'waitPay',
      component: WaitPay,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/consumptionDetails',
      name: 'consumptionDetails',
      component: ConsumptionDetails,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/mouthBill',
      name: 'mouthBill',
      component: MouthBill,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/myWallet',
      name: 'myWallet',
      component: MyWallet,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/MyParse',
      name: 'MyParse',
      component: MyParse,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/tixian',
      name: 'Tixian',
      component: Tixian,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/questionResult',
      name: 'questionResult',
      component: QuestionResult,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/questionHistory',
      name: 'questionHistory',
      component: QuestionHistory,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/joinApply',
      name: 'joinApply',
      component: JoinApply,
      meta: {
        title: '',
        keepAlive: true
      }
    },
    { // 
      path: '/readJoin',
      name: 'readJoin',
      component: ReadJoin,
      meta: {
        title: ''
      }
    },
    { // 
      path: '/userCenter',
      // name: 'userCenter',
      component: UserCenter,
      meta: {
        title: ''
      }
    },
    {
      path: '/doorOrder',
      component: DoorOrder,
      meta: {
        title: ''
      }
    },
    {
      path: '/SiteMap',
      component: SiteMap,
      meta: {
        title: ''
      }
    }
  ]
})



