<template>
<div>
    <div v-wechat-title="$route.meta.title"></div>
    <!-- <p></p> -->
    <div class="consumption_up">
        <img v-if="isChong" src="./img/chong.png" alt="">
        <img v-else src="./img/xiaofei.png" alt="">
        <p></p>
        <span></span>
        <p v-if="moneyList.total_money == ''">{{ moneyList.total_money }}</p>
        <p v-else>0</p>
    </div>
    <!--  -->
    <ul class="ad_money_wrap">
        <p v-if="moneyList.data.data == ''" style="font-size: 0.45rem;color: rgb(153, 153, 153);width: 92%;margin: 0 auto;padding-top: 0.55rem;opacity: 0.5;"></p>
        <li v-else v-for="(item,inex) in moneyList.data.data">
           <div class="adv_title">
                <span>{{ item.f_tips }}</span>
                <span v-if="isChong">+{{ item.money }}</span>
                <span v-else>-{{ item.money }}</span>
                <p>{{ item.create_time }}</p>
            </div>
       </li>
    </ul>
    <!--  -->
    <ul class="pay_center_box" v-show="show_ul">
       <li>
           <span></span>
           <span></span>
       </li>
       <li>
           <span>      </span>
           <span></span>
       </li>
       <li>
           <span></span>
           <span>2018-01-01 00:00:00</span>
       </li>
       <li>
           <span></span>
           <span>2018-01-01 00:00:00</span>
       </li>
       <li>
           <span></span>
           <span></span>
       </li>
       <li>
           <span></span>
           <span></span>
       </li>
       <li>
           <span></span>
           <span>2018-01-01 00:00:00</span>
       </li>
       <li>
           <span></span>
           <span>222222222222222222</span>
       </li>
       <li>
           <span></span>
           <span>222222222222222222</span>
       </li>
   </ul>
</div>
</template>

<script>
import { Indicator, MessageBox } from 'mint-ui'
export default {
  components: {
    Indicator,
    MessageBox
  },
  data () {
    return {
      show_ul: false,
      moneyList: '',
      isChong: true,
      detailData: ''
    }
  },
  methods: {
    getMoneyMsg: function (time, type) {
      this.$axios.get('/api/wxpub/user_wallet/showDetailByDay.html?time=' + time + '&io=' + type)
        .then((res) => {
          Indicator.close()
          this.moneyList = res.data.data
        })
        .catch((err) => {
          console.log(err)
        })
    }
  },
  beforeCreate: function () {

  },
  created: function () {
    let time = this.$route.query.code
    let type = this.$route.query.f_io
    console.log(type)
    if (type == 0) { // chong
      console.log('if')
      this.isChong = false
    } else {
      console.log('else')
    }
    Indicator.open()
    this.getMoneyMsg(time, type)
  },
  beforeMount: function () {

  },
  mounted: function () {

  },
  destroyed: function () {

  }
}
</script>

<style scoped>
@import './css/consumption_details.css';
</style>



