<template>
    <div class="create_phone_wrap">
        <div v-wechat-title="$route.meta.title"></div>
        <input v-model="advConentTitle" type="text" placeholder="">
        <ul>
           <li v-for="(item,index) in advData">
               <div class="adv_up">
                   <img @click="deleteConent(item.advIndex)" src="./img/close.png" alt="">
                   <img @click="moveConentDown(item,index)" src="./img/down.png" alt="">
                   <img @click="moveConentUp(item,index)" src="./img/up2.png" alt="">
               </div>
               <!--  -->
                <div class="uploadImg" style="position: relative;">
                    <input @change="upLoading($event,item)" style="z-index:10;" class="upload_file" id='uploadCompress' type="file" name="picture" accept="image/*">
                    <div class="up_text_wrap">
                        <img src="./img/add.png" alt="">
                    </div>
                    <p></p>
                    <img style="cursor:pointer;" class="upload_ctn_img" :src="item.advImg">
                </div>
                <!--  -->
                <div class="adv_add_text">
                    <textarea v-model="item.advConent" name="" id="" cols="30" rows="10" placeholder=""></textarea>
                </div>
           </li>
        </ul>
        <!--  -->
        <div class="create_new_content">
            <img @click="addNew()" src="./img/addadv.png" alt="">
        </div>
        <input v-model="srcTitle" type="text" placeholder="">
        <input v-model="advAdress" type="text" placeholder="">
        <input v-model="advTel" type="text" placeholder="">

        <div class="page_btn">
            <div @click="lookAdv($event)"></div>
            <div @click="advSubmit()"></div>
        </div>
    </div>
</template>

<script>
import { Indicator, MessageBox } from 'mint-ui'
export default {
  data () {
    return {
    //   imageUrl: require('./img/111.png'),
      appPic: '',
      advData: [
        {
          advIndex: 0,
          advImg: require('./img/111.png'),
          advConent: ''
        }
      ],
      cancelId: '',
      advConentTitle: '',
      srcTitle: '',
      advAdress: '',
      advTel: '',
      lookData: []
    }
  },
  methods: {
    // uptoken
    getToken: function () {
      // this.imgToken = '8RR89PskwpRkNF9qDp9n_mLkkQtrDa148VhwqKlr:5gmDRQjMbqVD4bD7_ERYiQbDIF8=:eyJzY29wZSI6Im5nY20iLCJkZWFkbGluZSI6MTUyODI2OTA3OH0='
      this.$axios.get('/api/service/adv_api/getuptoken')
        .then((res) => {
          this.imgToken = res.data.uptoken
          // if (res.data === 200) {
          //   this.imgToken = res.data.data.uptoken
          //   console.log(this.imgToken)
          // } else {

          // }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    // 
    upLoading: function (e, item) {
      Indicator.open()
      let token = this.imgToken
      let file = e.target.files[0]
      let param = new FormData() // form
      param.append('file', file, file.name)// appendform
      param.append('token', this.imgToken)
      param.append('chunk', '0')// form
      // console.log(param.get('file')) // FormDataget
      let config = {
        headers: {'Content-Type': 'multipart/form-data'}
      } //  http://up-z1.qiniu.com  http://upload.qiniu.com/
      this.$axios.post('http://upload-z1.qiniu.com/', param, config)
        .then(response => {
          console.log(response.data)
          Indicator.close()
          item.advImg = 'http://img.agrimedia.cn/' + response.data.key
          //   console.log(this.imageUrl)
          console.log(item.advImg)
        })
        .catch((err) => {
          MessageBox.alert('')
          Indicator.close()
        })
    },
    // 
    addNew: function () {
      this.advData.push(
        {
          advIndex: this.advData.length,
          advImg: require('./img/111.png'),
          advConent: ''
        }
      )
    },
    // 
    deleteConent: function (id) {
      this.cancelId = id
      this.advData.splice(this.advData.findIndex(item => item.advIndex === this.cancelId), 1)
    },
    //  
    moveConentUp: function (item, index) {
      // (, , )
      // array.splice(2, 0, "three");
      // console.log(index + '')

      // console.log(this.advData)
      if (index === 0) {
        console.log('')
      } else {
        // console.log('else')
        this.advData.splice(index - 1, 0, (this.advData[index])) // 
        this.advData.splice(index + 1, 1) // 
      }

      // console.log(this.advData)
    },
    // 
    moveConentDown: function (item, index) {
      // this.advData.splice(this.advData.findIndex(item => item.advIndex === this.index), 1)
      // this.advData.splice(index, 0, item)
      console.log(index)
      if (index === this.advData.length - 1) {
        console.log('')
      } else {
        this.advData.splice(index + 2, 0, this.advData[index]) // 
        this.advData.splice(index, 1) // 
      }
    },
    advSubmit: function () {
      let myreg = /^[1][3,4,5,7,8][0-9]{9}$/ // 
      if (this.advConentTitle === '') {
        MessageBox.alert('')
      } else if (this.srcTitle === '') {
        MessageBox.alert('')
      } else if (this.advAdress === '') {
        MessageBox.alert('')
      } else if (this.advTel === '') {
        MessageBox.alert('')
      } else if (!myreg.test(this.advTel)) {
        MessageBox.alert('')
      } else if (this.advData[0].advImg.substring(0, 4) !== 'http') {
        MessageBox.alert('')
      } else if (this.advData[0].advConent === '') {
        MessageBox.alert('')
      } else {
        // MessageBox.alert('')
        this.advAxios()
      }
    },
    advAxios: function () {
      let postData = this.$qs.stringify({
        h5_title: this.advConentTitle,
        h5_content: this.advData,
        h5_phone: this.advTel,
        h5_src_title: this.srcTitle,
        h5_address: this.advAdress,
        h5_app_pic: this.appPic
      })
      this.$axios({
        method: 'post',
        url: '/api/wxpub/create_adv/saveSecond',
        data: postData
      })
        .then((res) => {
          console.log(res)
          if (res.data.code === 200) {
            MessageBox.alert('')
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    lookAdv: function (e) {
      this.lookData.push(
        {
          type: 1
        },
        {
          h5_title: this.advConentTitle
        },
        {
          h5_content: this.advData
        },
        {
          h5_phone: this.advTel
        },
        {
          h5_src_title: this.srcTitle
        },
        {
          h5_address: this.advAdress
        }
      )
      // bus.$emit('LOOKDATA', this.lookData)
      // this.$store.dispatch('getUser', '')
      this.$store.dispatch('getUser', this.lookData)
      // console.log(this.lookData)

      let myreg = /^[1][3,4,5,7,8][0-9]{9}$/ // 
      if (this.advConentTitle === '') {
        MessageBox.alert('')
      } else if (this.srcTitle === '') {
        MessageBox.alert('')
      } else if (this.advAdress === '') {
        MessageBox.alert('')
      } else if (this.advTel === '') {
        MessageBox.alert('')
      } else if (!myreg.test(this.advTel)) {
        MessageBox.alert('')
      } else if (this.advData[0].advImg.substring(0, 4) !== 'http') {
        MessageBox.alert('')
      } else if (this.advData[0].advConent === '') {
        MessageBox.alert('')
      } else {
        let postData = this.$qs.stringify({
          h5_title: this.advConentTitle,
          h5_content: this.advData,
          h5_phone: this.advTel,
          h5_src_title: this.srcTitle,
          h5_address: this.advAdress,
          h5_app_pic: this.appPic
        })
        this.$axios({
          method: 'post',
          url: '/api/wxpub/create_adv/saveSecond',
          data: postData
        })
          .then((res) => {
            console.log(res)
            if (res.data.code === 200) {
              this.$router.push({
                path: '/landingPage',
                query: {
                  type: 1
                }
              })
            } else {
              MessageBox.alert(res.data.msg)
            }
          })
          .catch((err) => {
            MessageBox.alert('')
          })
      }
    }

  },

  beforeCreate: function () {
    // window.location.reload()
  },
  created: function () {
    // console.log(this.$route.query.imgUrl)
    this.appPic = this.$route.query.imgUrl //  
    this.advAdress = this.$route.query.title //  
    this.advTel = this.$route.query.tel // 
    this.getToken()
  },
  mounted: function () {

  },
  deactivated: function () {
    Indicator.close()
  }

}
</script>

<style scoped>
@import './css/create_phone_adv.css';
</style>



