<template>
<div>
    <div v-wechat-title="$route.meta.title"></div>
    <div id="map">
    <!-- <baidu-map class="bm-view" :center="center" @ready="handler" ak="V22GZGIsf9gR6G7d3L1KoZq4OVDAbsx2">
    </baidu-map> -->
    </div>
    <div class="site_details_wrap">
        <p>5126</p>
        <ul>
            <li>
                <p style="line-height: 0.2rem;font-size: 0.55rem;color: #5286EC;">153</p>
                <span style="display: block;margin-top: -0.4rem;padding-left: 0.3rem;color: #999999;font-size: 0.3rem;"></span>
            </li>
            <li>
                <p style="line-height: 0.2rem;font-size: 0.55rem;color: #5286EC;">62</p>
                <span style="display: block;margin-top: -0.4rem;padding-left: 0.3rem;color: #999999;font-size: 0.3rem;"></span>
            </li>
        </ul>
    </div>

    <div class="call_us_wrap" v-show="call_us">
        <div class="call_us_up">
            <p></p>
            <span>AGRI MEDIA</span>
        </div>
        <ul class="call_ul">
            <li>
                <p>{{ showComMsg.title }}</p>
                <img src="./img/hotel.png" alt="">
            </li>
            <li>
                <p>{{ showComMsg.tel }}</p>
                <img src="./img/tel.png" alt="">
            </li>
            <li>
                <p>{{ showComMsg.detail_area }}</p>
                <img src="./img/address.png" alt="">
            </li>
        </ul>
    </div>

</div>
</template>

<script>
// import BMap from 'BMap'
export default {
  props: { // data
    height: {
      type: Number,
      default: 300
    },
    longitude: 114.486491, // 
    latitude: 38.043232 // 
  },
  data () {
    return {
      show_map_msg: false,
      call_us: true,
      center: {lng: 0, lat: 0},
      autoLocationPoint: {lng: 0, lat: 0},
      initLocation: false,
      showComMsg: {
        'title': '',
        'tel': '400-069-0088',
        'detail_area': ''
      },
      markerArr: [
        // { title: '', point: '113.264531,23.157003', address: '', tel: '12306' },
        // { title: '', point: '113.330934,23.113401', address: ' ', tel: '18500000000' },
        // { title: '', point: '113.312213,23.147267', address: '', tel: '18500000000' },
        // { title: '', point: '113.372867,23.134274', address: '', tel: '18500000000' }

      ]
    }
  },
  methods: {
    creatMap: function () {

    },
    getSite: function () { // 
      let _this = this
      this.$axios.get('/api/wxpub/contact_us/index')
        .then(function (res) {
          if (res.data.code === 200) {
            let siteObj = res.data.data
            siteObj.forEach(item => {
              item.point = item.Lng + ',' + item.Lat
              item.title = item.detail_area
              item.tel = item.phone
            })
            // console.log(siteObj)
            _this.markerArr = siteObj
            _this.getMap()
          }
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    getMap: function () { // 
      var map = new BMap.Map('map') // Map
      var point = new BMap.Point(116.385685, 40.006454) // 
      map.centerAndZoom(point, 7) // ,
      map.enableScrollWheelZoom(true) // 
      // 
      var ctrlNav = new window.BMap.NavigationControl({
        anchor: BMAP_ANCHOR_TOP_LEFT,
        type: BMAP_NAVIGATION_CONTROL_LARGE
      })
      map.addControl(ctrlNav)

      // 
      var ctrlOve = new window.BMap.OverviewMapControl({
        anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
        isOpen: 1
      })
      map.addControl(ctrlOve)

      // 
      var ctrlSca = new window.BMap.ScaleControl({
        anchor: BMAP_ANCHOR_BOTTOM_LEFT
      })
      map.addControl(ctrlSca)

      var point = new Array() // 
      var marker = new Array() // 
      var info = new Array() // 
      let markerArr = this.markerArr
      for (var i = 0; i < markerArr.length; i++) {
        var p0 = markerArr[i].point.split(',')[0] //
        var p1 = markerArr[i].point.split(',')[1] // point
        point[i] = new window.BMap.Point(p0, p1) // 
        marker[i] = new window.BMap.Marker(point[i]) // 
        map.addOverlay(marker[i])
        marker[i].setAnimation(BMAP_ANIMATION_BOUNCE) // 
        var label = new window.BMap.Label(['' + markerArr[i].title + '</br>', '' + markerArr[i].tel], { offset: new window.BMap.Size(20, -10) })
        marker[i].setLabel(label)
        console.log(label)
        info[i] = new window.BMap.InfoWindow('<p style=font-size:12px;lineheight:1.8em;>' + markerArr[i].title + '</br>' + markerArr[i].address + '</br> ' + markerArr[i].tel + '</br></p>') // 
      }

      marker.forEach((item, index) => {
        let arrindex = index
        item.addEventListener('click', () => {
          // console.log(arrindex)
          // console.log(item)
          this.showComMsg = this.markerArr[arrindex]
          // console.log(this.markerArr[arrindex])
        })
      })
    }
  },
  beforeCreate: function () {

  },
  created: function () {
    this.getSite()
  },
  beforeMount: function () {

  },
  mounted: function () {
    // this.getMap()
  }
}
</script>

<style scoped>
@import './css/site_details.css';
.BMapLabel{
  border:none!important;
}
</style>



