<template>
<div>
    <div v-wechat-title="$route.meta.title"></div>
    <div class="read_join_bg">
        <!-- <img src="./img/banner.png" alt=""> -->
        <p> | </p>
    </div>
    <div class="read_text_box">
        <h2></h2>
        <h3></h3>
        <p>1</p>
        <p>2</p>
        <p>3</p>
        <p>4</p>
        <h3></h3>
        <p>1</p>
        <p>2</p>
        <h3></h3>
        <p>1</p>
        <p>2</p>
        <p>3</p>
        <p>4</p>
        <p>4.11063</p>
        <p>4.2</p>
    </div>
    <div class="join_submit_btn2" @click="goJoin()">
        
    </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      isRead: false
    }
  },
  methods: {
    goJoin: function () {
      this.$emit('isRead', this.isRead)
      this.$router.back(-1)
    }
  }
}
</script>

<style scoped>
.read_join_bg{
    width: 100%;
    height: 1.95rem;
    background: url('./img/banner.png') 100% 100%;
    background-size: cover;
}
.read_join_bg p{
    text-align: center;
    line-height: 1.95rem;
    font-size: 0.4rem;
    font-weight: 600;
    color: #fff;
}
.read_text_box{
    width: 84%;
    margin:0 auto;
    text-align: left;
}
.read_text_box h2{
    font-size: 0.45rem;
    font-weight: 600;
    margin: 0.5rem auto;
}
.read_text_box h3{
    font-size: 0.35rem;
    color: #151515;
    margin-bottom: 0.1rem;
}
.read_text_box p{
    font-size: 0.3rem;
    color: #666666;
    margin:0.1rem 0 0.3rem 0;
}
.join_submit_btn2 {
    width: 100%;
    height: 1.13rem;
    background: -webkit-gradient(linear, right top, left top, from(#4189D0), color-stop(68%, #5286EC));
    background: linear-gradient(-90deg, #4189D0 0%, #5286EC 68%);
    color: #fff;
    font-size: 0.4rem;
    line-height: 1.13rem;
    margin-top: 0.5rem;
    cursor: pointer;
}
</style>



