<template>
<div>
    <div v-wechat-title="$route.meta.title"></div>
        <div class="user_center_box">
      <div class="user_up_box">
        <img :src="userInfo.user_thumbnail" alt="">
        <p>{{ userInfo.user_nickname}}</p>
        <p v-if="userInfo.bindPhone != ''" >{{ userInfo.bindPhone}}</p>
        <p v-else class="bind_phone" @click="bindPhone()"></p>
      </div>
    </div>

    <ul class="user_cen_box">
      <li>
        <router-link to="/MyParse">
          <img src="./img/Rectangle 54.png" alt="">
          <p></p>
        </router-link>
      </li>
      <li>
        <router-link :to="{ path:'/myAdv', query: { type:  0} }">
          <img src="./img/my_love.png" alt="">
          <p></p>
        </router-link>
      </li>
      <li>
        <router-link to="/myAdv?type=1">
          <img style="width: 0.5rem;" src="./img/foot.png" alt="">
          <p></p>
        </router-link>
      </li>
      <li>
        <router-link to="/myAdv?type=2">
          <img src="./img/my_adv.png" alt="">
          <p></p>
        </router-link>
      </li>
      <li>
        <router-link to="/myOrder">
          <img src="./img/my_order.png" alt="">
          <p></p>
        </router-link>
      </li>
      <li>
        <router-link to="/siterPrivilege">
          <img src="./img/siter_t.png" alt="">
          <p></p>
        </router-link>
      </li>
    </ul>

    <div class="tab_line"></div>

    <div class="user_active">
      <div class="active_title">
        <img src="./img/siter_t.png" alt="">
        <p></p>
      </div>
      <ul v-show="kongAct" class="siter_active">
        <template>
          <swiper :options="swiperOption" ref="mySwiper">
            <!-- slides -->
            <swiper-slide><img style="width:100%;height:100%;" src="./img/user_bj.png" alt=""></swiper-slide>
            <swiper-slide>I'm Slide 2</swiper-slide>
            <swiper-slide>I'm Slide 3</swiper-slide>
            <swiper-slide>I'm Slide 4</swiper-slide>
            <swiper-slide>I'm Slide 5</swiper-slide>
            <swiper-slide>I'm Slide 6</swiper-slide>
            <swiper-slide>I'm Slide 7</swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination"  slot="pagination"></div>
            <div class="swiper-button-prev" slot="button-prev"></div>
            <div class="swiper-button-next" slot="button-next"></div>
            <div class="swiper-scrollbar"   slot="scrollbar"></div>
          </swiper>
        </template>
      </ul>
      <!-- <imgsrc="./img/show.png" alt=""> -->
      <p v-show="kongAct" class="check_more_submit" @click="toMore()"></p>
      <p v-show="!kongAct" class="check_more_submit" ></p>
    </div>
</div>
</template>

<script>
import slider from 'vue-concise-slider'// slider
import VueAwesomeSwiper from 'vue-awesome-swiper'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'carrousel',
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      kongAct: true,
      userInfo: '',
      promotion: '',
      swiperOption: {// swiperapihttp://www.swiper.com.cn/api/
        // notNextTicknotNextTicktrueNextTickswiperswiper<br>swipertrue
        notNextTick: true,
        // swiper configs swiperapi
        autoplay: 3000,
        direction: 'horizontal',
        grabCursor: true,
        setWrapperSize: true,
        autoHeight: true,
        pagination: '.swiper-pagination',
        paginationClickable: true,
        autoplay: false,
        speed: 900,
        loop: true,
        grabCursor: true,
        setWrapperSize: true,
        autoHeight: true,
        prevButton: '.swiper-button-prev', // 
        nextButton: '.swiper-button-next', // 
        scrollbar: '.swiper-scrollbar', // 
        mousewheelControl: true,
        observeParents: true,
        // debugger
        debugger: true
      }
    }
  },
  methods: {
    getuserInfo: function () {
      this.$axios.get('/api/wxpub/user/getuserinfo')
        .then((res) => {
          // console.log(res.data)
          //   resaxios  resdata 
          if (res.data.code === 200) {
            // code  code200
            // console.log(res.data.data.userInfo)
            this.userInfo = res.data.data.userInfo
            if (this.promotion.length == 0) {
              this.kongAct = false
            } else {
              this.promotion = res.data.data.promotion.data // 
            }
          }
        })
        .catch((err) => {
          console.error(err, '')
        })
    },
    // 
    bindPhone: function () {
      this.$router.push({
        path: '/phone'
      })
    },
    // myActivity 
    toMore: function () {
      this.$router.push({
        path: '/myActivity'
      })
    },
    // 
    nearlyActive: function () {
      // this.$axios.get('api')
    }
  },
  beforeCreate: function () {

  },
  created: function () {
    this.getuserInfo()
  },
  beforeMount: function () {

  },
  mounted: function () {
    // this.timeDown()
  }
}
</script>

<style scoped>
@import './css/user_center.css';
.siter_active{
    width: 100%;
    height: 4rem;
    overflow: hidden;
}
.siter_active li{
    height: 4rem;
    cursor: pointer;
}
.siter_active li img{
    width:100%;
    height: 100%;
}
.swiper-slide{
  float: left;
  height:4rem;
  overflow: hidden;
}
</style>



